'use client';

import { AdminHeader } from '@/components/admin/admin-header';
import { db, Order } from '@/lib/firebase';
import { doc, getDoc, updateDoc } from 'firebase/firestore';
import Link from 'next/link';
import { useParams, useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';

export default function EditOrder() {
  // Add children?: string[] to the local order type
  type OrderWithChildren = Order & { children?: string[] };
  const [order, setOrder] = useState<OrderWithChildren | null>(null);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const params = useParams();
  const router = useRouter();
  const orderId = params?.id as string;

  useEffect(() => {
    async function fetchOrder() {
      if (!orderId) return;
      
      setLoading(true);
      try {
        const orderDoc = await getDoc(doc(db, 'orders', orderId));
        if (orderDoc.exists()) {
          setOrder({ id: orderDoc.id, ...orderDoc.data() } as OrderWithChildren);
        } else {
          alert('Order not found');
          router.push('/admin/orders');
        }
      } catch (error) {
        console.error('Error fetching order:', error);
        alert('Error loading order');
      } finally {
        setLoading(false);
      }
    }

    fetchOrder();
  }, [orderId, router]);

  // Helper to get available children
  const getAvailableChildren = (order: OrderWithChildren) => {
    if (order.children && order.children.length > 0) {
      return order.children;
    }
    // fallback: unique child names from items
    const names = order.items.map(i => i.childName).filter(Boolean);
    return Array.from(new Set(names));
  };

  const handleChildChange = (itemIndex: number, childName: string) => {
    if (!order) return;
    
    const updatedItems = [...order.items];
    updatedItems[itemIndex] = { ...updatedItems[itemIndex], childName };
    setOrder({ ...order, items: updatedItems });
  };

  const handleSave = async () => {
    if (!order) return;
    
    setSaving(true);
    try {
      await updateDoc(doc(db, 'orders', orderId), {
        items: order.items
      });
      alert('Order updated successfully');
      router.push('/admin/orders');
    } catch (error) {
      console.error('Error updating order:', error);
      alert('Error updating order');
    } finally {
      setSaving(false);
    }
  };

  if (loading) {
    return (
      <div>
        <AdminHeader />
        <main className="container mx-auto px-4 py-12 max-w-xl">
          <div className="text-center">Loading...</div>
        </main>
      </div>
    );
  }

  if (!order) {
    return (
      <div>
        <AdminHeader />
        <main className="container mx-auto px-4 py-12 max-w-xl">
          <div className="text-center">Order not found</div>
        </main>
      </div>
    );
  }

  return (
    <div>
      <AdminHeader />
      <main className="container mx-auto px-4 py-12 max-w-xl">
        <h1 className="text-3xl font-headline font-bold text-primary mb-8 text-center">Edit Order</h1>
        
        <div className="bg-card rounded-lg shadow-md p-6 space-y-6">
          {/* Order Info */}
          <div className="space-y-4">
            <h2 className="text-xl font-semibold">Order Information</h2>
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium mb-1">Order ID</label>
                <input 
                  className="w-full p-2 border rounded bg-gray-50" 
                  value={order.id} 
                  disabled 
                />
              </div>
              <div>
                <label className="block text-sm font-medium mb-1">Parent Name</label>
                <input 
                  className="w-full p-2 border rounded bg-gray-50" 
                  value={order.parentName} 
                  disabled 
                />
              </div>
              <div>
                <label className="block text-sm font-medium mb-1">Parent Email</label>
                <input 
                  className="w-full p-2 border rounded bg-gray-50" 
                  value={order.parentEmail} 
                  disabled 
                />
              </div>
              <div>
                <label className="block text-sm font-medium mb-1">Total Amount</label>
                <input 
                  className="w-full p-2 border rounded bg-gray-50" 
                  value={`$${order.totalAmount.toFixed(2)}`} 
                  disabled 
                />
              </div>
            </div>
          </div>

          {/* Class Items */}
          <div className="space-y-4">
            <h2 className="text-xl font-semibold">Class Assignments</h2>
            <div className="space-y-4">
              {order.items.map((item, index) => (
                <div key={index} className="border rounded-lg p-4 space-y-3">
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <label className="block text-sm font-medium mb-1">Class ID</label>
                      <input 
                        className="w-full p-2 border rounded bg-gray-50" 
                        value={item.classId} 
                        disabled 
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium mb-1">Session Length</label>
                      <input 
                        className="w-full p-2 border rounded bg-gray-50" 
                        value={`${item.sessionLength} weeks`} 
                        disabled 
                      />
                    </div>
                  </div>
                  <div>
                    <label className="block text-sm font-medium mb-1">Assign Child</label>
                    <div className="flex flex-col gap-2">
                      {getAvailableChildren(order).length > 0 ? (
                        getAvailableChildren(order).map((child: string, childIdx: number) => (
                          <label key={childIdx} className="inline-flex items-center gap-2">
                            <input
                              type="radio"
                              name={`child-radio-${index}`}
                              value={child}
                              checked={item.childName === child}
                              onChange={() => handleChildChange(index, child)}
                            />
                            <span>{child}</span>
                          </label>
                        ))
                      ) : (
                        <span className="text-muted-foreground">No children available</span>
                      )}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Actions */}
          <div className="flex gap-4 pt-4">
            <button 
              className="bg-primary text-primary-foreground px-4 py-2 rounded font-semibold flex-1 hover:bg-primary/90 disabled:opacity-50"
              onClick={handleSave}
              disabled={saving}
            >
              {saving ? 'Saving...' : 'Save Changes'}
            </button>
            <Link 
              href="/admin/orders" 
              className="bg-gray-500 text-white px-4 py-2 rounded font-semibold flex-1 text-center hover:bg-gray-600"
            >
              Cancel
            </Link>
          </div>
        </div>
      </main>
    </div>
  );
} 